//公共方法
import Jsrsasign,{KJUR,hex2b64} from 'jsrsasign';
import axios from 'axios'
import store from '../../vuex/index'

let jwtServer = {}

/*
* get请求
* */
jwtServer.globalGetData = function (url) {
  let deferred = $.Deferred();
  let promise = deferred.promise;
  axios.get(url).then((data) => {
    deferred.resolve(data.data.response.result.main_data)
  }, (err) => {
    deferred.resolve(err)
  })
  return promise
}
/*
* post请求
* */
jwtServer.globalPostData = function (url, data) {
  let axiosDate = new Date()
  //新建一个Deferred 对象
  let deferred = $.Deferred();
  //原来的deferred对象上返回另一个deferred对象；没有参数时，返回一个新的deferred对象，该对象的运行状态无法被改变；接受参数时，作用为在参数对象上部署deferred接口。
  let promise = deferred.promise();
  axios.post(url,data).then((data) => {
    //改变Deferred对象的执行状态
    let oDate = new Date()
    let time = oDate.getTime() - axiosDate.getTime();
    (time > 500) ? time=time : time=500;
    setTimeout(() => {
      store.dispatch('USER_LOADING_STATE', false)
    }, time)
    deferred.resolve(data.data.response)
  }, (err) => {
    deferred.resolve(err);
  });
  return promise
}
/*
* 参数加签名
* */
jwtServer.addSign = function (params) {
  let prvKey = `-----BEGIN PRIVATE KEY----------END PRIVATE KEY-----`
  let sig = new KJUR.crypto.Signature({"alg": "SHA1withRSA","prov": "cryptojs/jsrsa"});
  sig.init(prvKey);
  sig.updateString(JSON.stringify(params));
  let signs = Jsrsasign.hex2b64(sig.sign())
  let sign = encodeURIComponent(signs ,"UTF-8")
  return sign
}
/**
 * 格式化数据
 */
jwtServer.submitData =function (params) {
  let submitData ={};
  submitData.header ={
    "clientid": "",
    "device": "pc",
    "platform": "web",
    "projectid": "project_platform",
    "version": "1.0"
  }
  submitData.request={}
  submitData.request = {"params":params}
  submitData.saveOperTokenCode =''
  submitData.tokenCode =''
  submitData.sign = jwtServer.addSign( submitData.request)
  return submitData
};
/**
 * 存储localStorage
 */
jwtServer.setStore = (name, content) => {
  if (!name) return;
  if (typeof content !== 'string') {
    content = JSON.stringify(content);
  }
  window.localStorage.setItem(name, content);
};

/**
 * 获取localStorage
 */
jwtServer.getStore = name => {
  if (!name) return;
  return window.localStorage.getItem(name);
};

/**
 * 删除localStorage
 */
jwtServer.removeStore = name => {
  if (!name) return;
  window.localStorage.removeItem(name);
};

export {jwtServer}






























