<template>
  <div class="register" v-title="'注册'">
    <!--banner开始-->
    <div class="banner"></div>
    <!--banner结束-->
    <div class="registerContainer">
      <div class="registerWarp">
        <!--注册输入区域开始-->
        <div class="registerCon">
          <input id="firstInput" type="text" placeholder="请输入您的手机号" v-model="phoneNumber" v-on:blur="judgePhoneNumber()" maxlength="11">
          <span class="errorMessage">请输入正确的手机号</span>
          <div class="code">
            <input type="text" placeholder="请输入验证码" id="code" maxlength="6">
            <a href="javascript:;" class="getCode" v-if="show" @click="getCode">获取验证码</a>
            <span class="countDown" v-if="!show"><span class="wait_seconds">{{count}}后重新获取</span></span>
          </div>
          <input type="password" placeholder="请设置8-16位字符的登录密码" maxlength="16">
          <input type="password" placeholder="请确认登录密码" maxlength="16">
          <p class="agreement"><span class="buttonOne button" v-if="!agree" @click="agreement"></span><span
            class="buttonTwo button" v-if="agree" @click="agreement"></span>阅读并同意 <span
            class="agreementCon">《金梧桐注册协议》</span></p>
          <input type="button" value="马上注册领红包" id="register">
        </div>
        <!--注册输入区域结束-->
        <!--理财推荐区域开始-->
        <div class="arrange">
          <div class="arrangeTitle"></div>
          <div class="financingCon">
            <ul>
              <li style="margin:-0.08rem 0.5rem 0 0.5rem;">
                <p><span style="font-size:.8rem;font-weight: 800;color:#fb4774;line-height: .8rem">8</span><span
                  style="font-size:.4rem;color:#fb4774;">%</span></p>
                <p style="color:#666;font-size: .22rem">年化收益率</p>
              </li>
              <li style="margin-top: .55rem;margin-right: .5rem;">
                <p style="font-size:.38rem;font-weight: 800;color:#333;margin-top:.2rem;line-height: 0">天天盈</p>
                <img style="width: 1.84rem;height: .32rem;margin-bottom: .3rem;" src="../../assets/images/register/day.png"
                     title=""/>
              </li>
              <li style="margin-top: .8rem;">
                <p class="atOnce" @click="bankTop">立即投资</p>
              </li>
            </ul>
          </div>
          <div class="montent">
            <ul style="margin-top:.34rem;">
              <li>
                <p style="font-size:.38rem;font-weight: 800;color:#333;">
                  <span>月月盈</span>
                  <br>
                  <img style="width: 1.84rem;height: .32rem ;margin-top:.1rem;"
                       src="../../assets/images/register/mont.png" title=""/>
                </p>
              </li>
              <li>
                <p style="font-size: 0">
                  <span style="font-size:.56rem;font-weight: 800;color:#fb4747;">10</span><span
                  style="font-size:.36rem;color:#fb4747;">%~</span><span
                  style="font-size:.56rem;font-weight: 800;color:#fb4747;">14</span><span
                  style="font-size:.36rem;color:#fb4747;">%</span>
                </p>
                <p style="font-size:.22rem;width:100%;text-align:center; color:#666">年化收益率</p>
              </li>
              <li>
                <p class="atOnce" style="margin: .2rem auto 0 auto;height:.6rem;width:1.7rem;line-height: .6rem;"
                   @click="bankTop">立即投资</p></li>
            </ul>
          </div>
          <div class="montent montent1">
            <ul style="margin-top:.34rem;">
              <li>
                <p style="font-size:.38rem;font-weight: 800;color:#333;">
                  <span>定存盈</span>
                  <br>
                  <img style="width: 1.84rem;height: .32rem ;margin-top:.1rem;"
                       src="../../assets/images/register/dingcun.png" title=""/>
                </p>
              </li>
              <li>
                <p style="font-size: 0">
                  <span style="font-size:.56rem;font-weight: 800;color:#fb4747;">10</span><span
                  style="font-size:.36rem;color:#fb4747;">%~</span><span
                  style="font-size:.56rem;font-weight: 800;color:#fb4747;">14</span><span
                  style="font-size:.36rem;color:#fb4747;">%</span>
                </p>
                <p style="font-size:.22rem;width:100%;text-align:center; color:#666">年化收益率</p>
              </li>
              <li>
                <p class="atOnce" style="margin: .2rem auto 0 auto;height:.6rem;width:1.7rem;line-height: .6rem;"
                   @click="bankTop">立即投资</p></li>
            </ul>
          </div>
        </div>

        <!--理财推荐区域结束-->
        <div style="clear: both;"></div>
        <!--三重保障区域开始-->
        <div class="ensure">
          <img src="../../assets/images/register/ensure.png" alt="">
        </div>
        <!--三重保障区域结束-->

      </div>
      <!--产品优势区域开始-->
      <div class="goodNess">
        <img src="../../assets/images/register/goodNess.png" alt="">
      </div>
      <!--产品优势区域结束-->
      <!--底部区域开始-->
      <div class="footer">
        <ul>
          <li class="setList">
            <div>
              <ul class="photoList">
                <li>
                  <img src="../../assets/images/register/phone.png" title=""/>
                  <p>手机官网</p>
                </li>
                <li>
                  <a href="javascript:;"><img src="../../assets/images/register/app.png" title=""/></a>
                  <p>下载APP</p>
                </li>
                <li style="margin-right: 0">
                  <img src="../../assets/images/register/ewm.png" title=""/>
                  <p>公众号</p>
                </li>
              </ul>
            </div>
          </li>
          <li>&copy;2018金梧桐All Rights Reserved</li>
          <li>北京普惠通数据信息技术有限公司</li>
          <li style="border: 0">投资有风险&nbsp;&nbsp;出借需谨慎</li>
        </ul>
      </div>
      <!--底部区域结束-->

    </div>
  </div>
</template>

<script>
  import { jwtServer } from '../../assets/js/jwtServers'
  export default {
    data() {
      return {
        show: true,
        count: '',
        timer: null,
        agree: false,
        phoneNumber:''

      }
    },
    methods: {
      getCode() {
        const TIME_COUNT = 60;
        if (!this.timer) {
          this.count = TIME_COUNT;
          this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              this.show = true;
              clearInterval(this.timer);
              this.timer = null;
            }
          }, 1000)
        }
      },
      agreement() {
        (this.agree == false) ? this.agree = true : this.agree = false;
      },
      bankTop() {
        scrollTo(0, 0);
      },
      judgePhoneNumber(){
        let phoneNumber =this.phoneNumber;
        jwtServer.globalPostData('/apis/msgcenter/query/1.0/sendMessageMobileValidCode/1.0',jwtServer.submitData({"MOBILE":phoneNumber,"USER_TYPE":"1","VALID_TYPE":"0"})).then((data)=> {
          console.log(data.status)
        });

      }
    }

  }
</script>

<style lang="less" scoped>
  /*公共css开始*/
  .banner {
    width: 7.5rem;
    height: 3.75rem;
    background: url("../../../src/assets/images/register/register_banner.png") no-repeat center;
    background-size: 100%;
  }

  .registerContainer {
    width: 7.5rem;
    height: auto;
    background: #f45b2b;
    font-family: "微软雅黑";
  }

  .registerWarp {
    width: 6.8rem;
    height: auto;
    margin: 0 auto;
  }

  .registerCon {
    width: 100%;
    height: 7rem;
    background: #fff;
    border-radius: .1rem;
    overflow: auto;
  }

  /*公共css结束*/
  /*注册输入区域开始*/
  .registerCon input {
    width: 5.9rem;
    height: .84rem;
    margin: 0 auto;
    display: block;
    line-height: .84rem;
    border-radius: .1rem;
    border: .02rem solid #ddd;
    margin-top: .3rem;
    outline: none;
    text-indent: .15rem;
    font-size: .28rem;
    color: #333;
  }

  .code {
    width: 5.9rem;
    height: .84rem;
    margin: .3rem auto 0 auto;
  }

  #firstInput {
    margin: .4rem auto 0 auto;
  }

  /*修改placeholder的颜色*/
  .registerCon input::-webkit-input-placeholder { /* WebKit browsers */
    color: #999;
  }

  .registerCon input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color: #999;
  }

  .registerCon input::-moz-placeholder { /* Mozilla Firefox 19+ */
    color: #999;
  }

  .registerCon input:-ms-input-placeholder { /* Internet Explorer 10+ */
    color: #999;
  }

  #code {
    width: 3.45rem;
    margin: 0 0 0.8rem 0;
    display: inline-block;
  }

  .getCode {
    display: inline-block;
    width: 2.3rem;
    height: 100%;
    font-size: .28rem;
    line-height: .86rem;
    text-align: center;
    background: #eee;
    float: right;
    border: .02rem solid #ddd;
    border-radius: .1rem;
    color: #666;
    text-decoration: none;
  }

  .countDown {
    display: inline-block;
    width: 2.3rem;
    height: 100%;
    font-size: .28rem;
    line-height: .86rem;
    text-align: center;
    background: #eee;
    float: right;
    border: .02rem solid #ddd;
    border-radius: .1rem;
    color: #666;
    text-decoration: none;
  }

  .agreement {
    width: 5.9rem;
    margin: .2rem auto 0 auto;
    font-size: .26rem;
    color: #666;
  }

  .agreementCon {
    color: #fb4774;
  }

  #register {
    color: #fff;
    height: .86rem;
    background: #ff8a00;
  }

  .agreement .button {
    display: inline-block;
    width: .34rem;
    height: .34rem;
    background-size: 100%;
    vertical-align: middle;
    margin-right: .14rem;
  }

  .buttonOne {
    background: url("../../assets/images/register/buttonOne.png");
  }

  .buttonTwo {
    background: url("../../assets/images/register/buttonTwo.png");
  }

  /*注册输入区域结束*/
  /*理财推荐区域开始*/
  .arrange {
    width: 100%;
    height: auto;
  }

  .arrangeTitle {
    width: 2.43rem;
    height: .37rem;
    margin: .7rem auto .3rem auto;
    background: url("../../assets/images/register/arrangeSelect.png");
    background-size: 100%;
  }

  .day {
    height: 1.8rem;
    width: 100%;
    background: #fff;
    margin-top: .3rem;
    overflow: auto;
    text-align: center;
  }

  /*理财推荐区域结束*/
  /*三重保障区域开始*/
  .ensure {
    margin: .6rem 0 0 0;
  }

  .ensure img {
    width: 100%;
    height: 3.05rem;
  }

  /*三重保障区域结束*/

  /*产品优势区域开始*/
  .goodNess {
    margin: .6rem auto 0 auto;
    width: 6.9rem;
  }

  .goodNess img {
    width: 6.9rem;
    height: 3.77rem;
  }

  /*产品优势区域结束*/

  /*底部区域开始*/
  .footer {
    height: 4.2rem;
    width: 100%;
    background: #303334;
    font-size: .22rem;
    color: #666;
  }

  .footer ul li {
    line-height: .6rem;
    border-bottom: .01rem solid #444;
    text-align: center;
  }

  .footer .photoList li {
    float: left;
    margin: .38rem 1.1rem 0 0;
    border: 0;
    line-height: .3rem;
  }

  .setList {
    height: 1.97rem;
  }

  .photoList {
    margin-left: 1.38rem;
  }

  .photoList img {
    height: .88rem;
    width: .88rem;
  }

  /*底部区域结束*/
  .financingCon ul li {
    float: left;
  }

  .atOnce {
    display: block;
    width: 1.7rem;
    height: .6rem;
    border-radius: .1rem;
    background: #fb4747;
    line-height: .6rem;
    color: #fff;
    font-size: .24rem;
  }

  .atOnce:hover {
    background: #cd0000;
  }

  .montent {
    width: 3.3rem;
    height: 3.5rem;
    background: #fff;
    float: left;
    margin-top: .2rem;
    text-algin: center;
    over-flow: auto;
  }

  .montent ul {
    width: 100%;
  }

  .montent ul li {
    width: 100%;
  }

  .montent ul li p {
