  }

  .montent1 {
    width: 3.3rem;
    height: 3.5rem;
    background: #fff;
    float: right;
    margin-top: .2rem;
    text-algin: center;
    over-flow: auto;
  }

  .financingCon {
    height: 1.8rem;
    width: 100%;
    background: #fff;
    margin-top: .3rem;
    overflow: hidden;
    text-align: center;
  }
  /*提示信息*/
  .errorMessage{
    font-size: .2rem;
    color: #fb4747;
    position: absolute;
    left: .8rem;
  }
</style>



